.body {
    position: relative;
    min-width: 320px;
    background-color: black;
    padding: 5px;
}
.body--full-screen {
    height: 100vh;
}
.clearfix:after {
    display: block;
    content: "";
    clear: both;
}
.hero-item {
    position: relative;
    margin: 10px 0;
    border: 3px solid yellow;
    background-color: black;
    color: yellow;
}
.hero-img {
    position: absolute;
    top: 50%;
    left: 10px;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    width: 100px;
    height: 100px;
    border: 3px solid yellow;
    background: url("../img/person.jpg"), no-repeat, center;
    background-size: cover;
}
.hero-info {
    float: left;
    margin-left: 120px;
    font-weight: 800;
    font-size: 14px;
}
.hero-info span {
    font-style: italic;
    font-size: 12px;
}
.hero-info p {
    margin: 10px 0;
}
.next,
.previous {
    width: 120px;
    font-size: 20px;
    padding: 10px;
    border: 3px solid yellow;
    border-radius: 20px;
    background-color: black;
    color: yellow;
}
.next {
    float: right;
}
.previous {
    float: left;
}
.navigation {
    padding: 10px 0;
}
.disabled {
    opacity: 0.65;
    cursor: not-allowed
}
.hide {
    display: none;
}
.loading {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    width: 90%;
    height: 300px;
    background: url("../img/loading.png"), no-repeat, center;
    background-size: contain;
}
@media screen and (min-width: 768px){
    .body {
        padding: 20px;
    }
    .hero-item {
        width: 49%;
    }
    .hero-item:nth-child(odd) {
        float: right;
    }
    .hero-item:nth-child(even) {
        float: left;
    }
    .navigation button {
        margin-top: 40px;
        margin-bottom: 20px;
    }
    .previous {
        margin-left: 20%;
    }
    .next {
        margin-right: 20%;
    }
}
/* http://meyerweb.com/eric/tools/css/reset/
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
    margin: 0;
    padding: 0;
    border: 0;
    font-size: 100%;
    vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, menu, nav, section {
    display: block;
}
body {
    line-height: 1;
}
ol, ul {
    list-style: none;
}
blockquote, q {
    quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
    content: '';
}
table {
    border-collapse: collapse;
    border-spacing: 0;
}

hr {
    margin: 0;
    height: 8px;
}

a {
    text-decoration: none;
}

button {
    border: none;
}

* {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}